body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.App {
  text-align: center;
  box-sizing: border-box; }

/* header */
.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 22px;
  color: black; }

.App-title {
  margin-bottom: 40px; }

.header__search-field {
  width: 100%; }

.header__search-input {
  width: 60%;
  height: 30px;
  font-size: 16px;
  outline: 1px solid black;
  margin-bottom: 40px; }
  .header__search-input::-webkit-input-placeholder {
    padding-left: 5px;
    text-transform: capitalize; }
  .header__search-input::-ms-input-placeholder {
    padding-left: 5px;
    text-transform: capitalize; }
  .header__search-input::placeholder {
    padding-left: 5px;
    text-transform: capitalize; }

.main-list {
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0; }

.main-list__link {
  text-decoration: none; }

.main-list__item-card {
  background-color: #FE7E56;
  color: white;
  border: 1px solid black;
  margin: 10px;
  max-width: 160px;
  height: 350px;
  position: relative; }

.main-list__item-img {
  width: 100%;
  height: 230px; }

.main-list__item-name {
  margin: 5px 15px;
  font-size: 20px; }

.main-list__item-house {
  font-size: 16px;
  position: absolute;
  bottom: 0;
  width: 100%;
  margin: 8px auto; }

.spinner {
  margin-top: 300px;
  position: relative;
  left: 50%;
  width: 30px;
  height: 30px;
  -webkit-animation: spinner 0.75s linear infinite;
          animation: spinner 0.75s linear infinite; }
  .spinner span {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 50%; }
  .spinner .spinner-inner-1 {
    background: linear-gradient(to right, rgba(250, 128, 114, 0) 0%, rgba(250, 128, 114, 0) 50%, salmon 51%); }
  .spinner .spinner-inner-2 {
    background: linear-gradient(to top, rgba(255, 255, 255, 0) 0%, white 100%); }
  .spinner .spinner-inner-3 {
    top: 3px;
    left: 3px;
    width: 24px;
    height: 24px;
    background: white; }

@-webkit-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.btn--home {
  width: auto;
  margin-top: 30px;
  padding: 5px 10px;
  background-color: #FE7E56;
  color: white;
  border-radius: 5px;
  border: 1px solid black;
  font-size: 16px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1); }

.card {
  background-color: #F1FFFF;
  padding: 50px 5px; }

.card__div {
  background-color: #FE7E56;
  color: white;
  border: 1px solid black;
  margin: auto;
  height: 200px;
  max-width: 400px;
  display: flex; }

.card__div-img {
  height: 100%;
  max-width: 130px; }

.card__div--name {
  margin-left: auto;
  padding-left: 30px;
  font-size: 20px; }

.card__div--text {
  text-align: left;
  font-size: 16px; }
  .card__div--text p {
    margin: 7px; }

.card__div--span {
  display: inline-block; }

